:root {
  --bg-penee: #72FF06;
  --bg-lucky: #21BACB;
  --bg-neutral: #faf3e0;
  --text-primary: #9C0981;
  --text-penee: #9C0981;
  --text-lucky: #2ecc71;
}

html {
  box-sizing: border-box;
  font-size: 100%;
  background-color: var(--bg-neutral);
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

menu:not(article menu),
ol:not(article ol),
ul:not(article ul) {
  list-style: none;
}

menu,
ol,
ul {
  padding-left: 0;
}

article ol,
article ul {
  list-style-position: inside;
}

button {
  background: none;
  background-color: var(--button-main);
  color: var(--text-secondary);
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: var(--body-font);
  background-color: var(--bg-neutral);
  color: var(--text-primary);
  margin-inline: auto;
  max-width: min(100%, 1440px);
  box-shadow: 0 4px 100px rgba(0, 0, 0, 0.25);
}

textarea {
  max-width: 100%;
}

p {
  font-size: 2rem;
}

:root {
  --heading-font: "Inter", sans-serif;
  --body-font: "Inter", sans-serif;
}

#audioPlayer {
  display: none;
}

#audioImage {
  cursor: pointer;
  position: absolute;
}

.external-padding {
  padding: 2rem 1rem;
}
@media (min-width: 64em) {
  .external-padding {
    padding: 10rem 7rem;
  }
}

@media (min-width: 64em) {
  .heading {
    font-size: 6rem;
    font-weight: 700;
    line-height: 1.2;
  }
}

.justify-left {
  justify-self: left;
}

.justify-right {
  justify-self: right;
}

.audio-image {
  width: 5rem;
}
@media (min-width: 64em) {
  .audio-image {
    width: 16rem;
  }
}

.navigate-back-link {
  display: block;
  position: sticky;
  top: 1rem;
  left: 2rem;
  width: 3rem;
  z-index: 5;
}
@media (min-width: 64em) {
  .navigate-back-link {
    width: 7rem;
    top: 0;
    left: 7rem;
  }
}

.penee-bg-color {
  background-color: var(--bg-penee);
}

.lucky-bg-color {
  background-color: var(--bg-lucky);
}

h1,
h2,
h3 {
  font-weight: 700;
  margin: 0;
  font-family: var(--heading-font);
}

h1 {
  font-size: 1.5rem;
}
@media (min-width: 64em) {
  h1 {
    font-size: 6.25rem;
  }
}

h2 {
  font-size: 1.125rem;
}
@media (min-width: 64em) {
  h2 {
    font-size: 4.25rem;
  }
}

p {
  margin-top: 0;
  font-size: 0.75rem;
}
@media (min-width: 64em) {
  p {
    font-size: 2rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

a {
  color: inherit;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

menu:not(article menu),
ol:not(article ol),
ul:not(article ul) {
  list-style: none;
}

menu,
ol,
ul {
  padding-left: 0;
}

article ol,
article ul {
  list-style-position: inside;
}

label,
button,
select,
summary,
[type=radio],
[type=submit],
[type=checkbox] {
  cursor: pointer;
}

dialog {
  background-color: inherit;
  border: none;
  padding: 0;
}

dialog:focus-visible {
  outline: none;
}

dialog::backdrop {
  background-color: rgba(0, 0, 0, 0.9);
}

.animal-gallery {
  min-height: 100vh;
  background-color: rgba(74, 255, 107, 0.26);
}
.animal-gallery__heading {
  margin-block-end: 1rem;
}
@media (min-width: 64em) {
  .animal-gallery__heading {
    margin-block-end: 5rem;
  }
}
.animal-gallery__link-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 1.875rem;
  margin-block-end: 2rem;
}
@media (min-width: 64em) {
  .animal-gallery__link-wrapper {
    column-gap: 3.75rem;
    margin-block-end: 7rem;
  }
}
.animal-gallery__image {
  width: 8.125rem;
}
@media (min-width: 64em) {
  .animal-gallery__image {
    width: 37.5rem;
  }
}
.animal-gallery__animal-name {
  text-decoration: underline;
}
.animal-gallery__welcome-text {
  font-size: 1.5rem;
  font-weight: 700;
  max-width: 12.5rem;
  margin: 0;
}
@media (min-width: 64em) {
  .animal-gallery__welcome-text {
    font-size: 4.25rem;
    max-width: 34rem;
  }
}
.animal-gallery__audio-image {
  top: 9rem;
  left: calc(50% + 2.5rem);
  rotate: -2deg;
}
@media (min-width: 64em) {
  .animal-gallery__audio-image {
    top: 38rem;
    left: calc(50% + 8rem);
  }
}

.toy {
  min-height: 100vh;
  position: relative;
}
.toy__container {
  display: grid;
  position: relative;
  grid-template-rows: auto auto;
  grid-template-columns: 49% auto;
  grid-template-areas: "title title" "image text";
  align-items: start;
  margin-block-end: 1.5rem;
  width: 100%;
  min-height: 9rem;
}
@media (min-width: 64em) {
  .toy__container {
    column-gap: 3rem;
    margin-block-end: 4rem;
    min-height: 48rem;
    grid-template-rows: 45% auto;
    grid-template-columns: 49% auto;
    grid-template-areas: "image title" "image text";
  }
}
.toy__container > * {
  z-index: 4;
}
.toy__audio-image {
  top: 9rem;
  left: calc(50% + 2.5rem);
  rotate: -2deg;
}
@media (min-width: 64em) {
  .toy__audio-image {
    top: 38rem;
    left: calc(50% + 8rem);
  }
}
.toy__image {
  grid-area: image;
  max-width: 100%;
  padding-inline-end: 1.25rem;
}
@media (min-width: 64em) {
  .toy__image {
    max-width: 36rem;
    align-self: start;
    width: 100%;
    padding-inline-end: 0;
  }
}
.toy__img-arrow {
  display: none;
}
@media (min-width: 64em) {
  .toy__img-arrow {
    display: block;
    position: absolute;
    top: 15rem;
    left: calc(50% + 5rem);
  }
}
.toy__img-arrow--lucky {
  top: 5rem;
  left: calc(50% - 0.5rem);
}
.toy__img-divider {
  display: block;
  position: absolute;
  z-index: 2;
  width: 100%;
  top: 12rem;
}
@media (min-width: 64em) {
  .toy__img-divider {
    top: 31rem;
  }
}
.toy__heading {
  grid-area: title;
  margin-inline-start: 35%;
  margin-block-end: 1rem;
}
@media (min-width: 64em) {
  .toy__heading {
    max-width: 36rem;
    margin-inline-start: 0;
  }
}
@media (min-width: 64em) {
  .toy__heading--lucky {
    font-size: 4rem;
    padding-inline-start: 9rem;
  }
}
@media (min-width: 90em) {
  .toy__heading--lucky {
    font-size: 6rem;
    padding-inline-start: 10rem;
  }
}
.toy__text {
  grid-area: text;
  max-width: 8rem;
}
@media (min-width: 64em) {
  .toy__text {
    max-width: 100%;
    margin-block-start: 3rem;
    width: 70%;
  }
}
.toy__gallery-container {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-block-end: 2.75rem;
}
@media (min-width: 64em) {
  .toy__gallery-container {
    gap: 2.5rem;
    margin-block-end: 4rem;
  }
}
.toy__image-small {
  max-width: 5rem;
}
@media (min-width: 64em) {
  .toy__image-small {
    max-width: 12.5rem;
  }
}
.toy__image-small:last-of-type {
  border-radius: 20px;
}
.toy__container-secondary {
  display: flex;
  flex-direction: row;
  column-gap: 1rem;
  width: 100%;
}
@media (min-width: 64em) {
  .toy__container-secondary {
    column-gap: 5rem;
  }
}
.toy__image-paper {
  width: 40%;
  padding-block-start: 4rem;
}
@media (min-width: 64em) {
  .toy__image-paper {
    max-width: 37.5rem;
    padding-block-start: 0;
    object-fit: scale-down;
  }
}
.toy__container-description-wrapper {
  display: flex;
  flex-direction: column;
  row-gap: 3.5rem;
  position: relative;
}
@media (min-width: 64em) {
  .toy__container-description-wrapper {
    row-gap: 7rem;
  }
}
.toy__text-scribble {
  max-width: 8rem;
}
@media (min-width: 64em) {
  .toy__text-scribble {
    max-width: 28rem;
  }
}
.toy__img-scribble--penee {
  max-width: 4.5rem;
  transform: translate(-0.2rem);
}
@media (min-width: 90em) {
  .toy__img-scribble--penee {
    max-width: 18rem;
    transform: translate(-3rem);
  }
}
